.App {
  text-align: center;
}

.black-nav {
  display: flex;
  background: black;
  width: 100%;
  color: white;
  padding-left: 20px;
}

div {
  box-sizing: border-box;
}

.noWrap {
  display: inline-block;
  padding: 0px 5px 0px 5px;
}

.contentTitle {
  cursor: pointer;
  display: inline-block;
  font-size: 18px;
  font-weight: bold;
  padding-top: 13px;
}

.list {
  display: flex;
  flex-direction: column;
  background-color:#eeeeee;
}

.dabong {
  display: flex;
  align-items: center;
  justify-content: flex-end; /* 내부 요소들 오른쪽에서 왼쪽으로 정렬 */
  gap: 8px; /* 요소 사이 간격 설정 */
}

.dabongBtn {
  padding: 5px;
  background-color: azure;
  border-radius: 5px;
}

.delBtn {
  background-color: coral;
  color: #eee;
  border-radius: 5px;
}


.modal {
  margin-top: 20px;
  padding: 20px;
  background-color: #eee;
  text-align: left;
}

.list {
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid grey;
}

.border {
  text-align: left;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
